import {guid} from '@/utils/uuid-util'
import {dealLog} from "@/utils/log-util";
import {getCountryNameByDomain} from './utils/phone-number-util'
import {loadZbaseConfigToStorage} from "zbase-popup-component/src/zbase/popup/utils/ZbasePopupContentUtil";
import {setInstallTime, updateSetInstallTime} from "zbase-popup-component/src/zbase/popup/utils/installTime";
import {EXTENSION_NAME, WAPLUS_REMOVE_FLAG,EXTENSION_ID} from "@/utils/extension-util";

let extension_id = EXTENSION_ID
let zbaseConfigUrl = 'https://waplus.io/user/api-chrome-extension/get-remote-config'
//测试地址
// let zbaseConfigUrl = 'https://dev.zfscrmteam.top/user/api-chrome-extension/get-remote-config'
chrome.runtime.onMessage.addListener(function (data) {
    if (data.zbaseType === 'zbase-content-init') {
        if(!WAPLUS_REMOVE_FLAG) {
            loadZbaseConfigToStorage(extension_id, zbaseConfigUrl)
            // 當前安裝的插件ID，用於判斷是否為當前插件，其他插件判断是否安装
            chrome.storage.sync.get(['wa_identity'], function (res) {
                let _set = new Set([...res.wa_identity||[], extension_id]);
                chrome.storage.sync.set({'wa_identity': [..._set], 'wa_self': extension_id})
            })
        }
    }
    if (data.userPhoneNum && !data.urlStageFlag) {
        let PhoneNumber = require('awesome-phonenumber');
        let c = PhoneNumber('+' + data.userPhoneNum).getRegionCode(); // -> 'SE'
        let pn = new PhoneNumber(data.userPhoneNum.toString(), c);
        let region = pn.getRegionCode() ? pn.getRegionCode() : '';
        let countryCode = getCountryNameByDomain(region) ? getCountryNameByDomain(region) : '';
        let phoneNum = data.userPhoneNum;
        chrome.storage.local.get(['installTime','uuid','version'], function (res) {
            let installTime = res.installTime !== undefined ? res.installTime : '';
            let uuid = res.uuid !== undefined ? res.uuid : '';
            let version = res.version !== undefined ? res.version : '';
            let version_current = chrome.runtime.getManifest().version;
            let uninstall_url =
                'https://wawebsender.com/waplus-sender-uninstall/?utm_source=' +
                phoneNum +
                '&install_time=' +
                installTime +
                '&country_name=' +
                countryCode +
                '&uuid=' +
                uuid +
                '&version=' +
                version +
                '&version_current='+
                version_current+
                '&extension_name='+
                EXTENSION_NAME
            if(!WAPLUS_REMOVE_FLAG){
                //todo
                chrome.runtime.setUninstallURL(uninstall_url);
            }
        })
    }
    if(data.installLogFlag){
        chrome.storage.local.get(['installTime','uuid','version'], function (res) {
            let installTime = res.installTime !== undefined ? res.installTime : '';
            let uuid = res.uuid !== undefined ? res.uuid : '';
            let version = res.version !== undefined ? res.version : '';
            let version_current = chrome.runtime.getManifest().version;
            let uninstall_url =
                'https://wawebsender.com/waplus-sender-uninstall/?utm_source=300012' +
                '&install_time=' +
                installTime +
                '&uuid=' +
                uuid +
                '&version=' +
                version +
                '&version_current='+
                version_current+
                '&extension_name='+
                EXTENSION_NAME
            if(!WAPLUS_REMOVE_FLAG){
                //todo
                chrome.runtime.setUninstallURL(uninstall_url);
            }
        })
    }
    //处理阿里云日志
    if (data.type === 'log') {
        dealLog(data);
    }
    // 接收popup的请求，打开新的标签也
    if(data.type === 'url'){
        chrome.tabs.create({
            url:data.data,
            active:true
        })
    }
})
chrome.runtime.onInstalled.addListener(details => {
    //如果用户之前没有邀评，需要加上邀评记录时间
    chrome.storage.local.get(['inviteCommentTime','uuid','version'], function (res) {
        if (!res.inviteCommentTime) {
            chrome.storage.local.set({inviteCommentTime: Math.round(new Date() / 1000)});
        }
        let uuid = res.uuid !== undefined ? res.uuid : guid();
        let version = res.version !== undefined ? res.version : chrome.runtime.getManifest().version;
        let installTime =  Math.round(new Date() / 1000)
        if (details.reason === 'install') {
            if(!WAPLUS_REMOVE_FLAG) {
                loadZbaseConfigToStorage(extension_id, zbaseConfigUrl)
            }
            // 从组件中引入的
            setInstallTime(installTime)
            chrome.storage.local.set({installTime,uuid,version,installLogFlag:false});
            dealLog({type: 'log', 'eventType': 300008,'otherParams':{uuid,version}});
            const tabUrl = 'https://web.whatsapp.com'
            chrome.tabs.create({
                url: tabUrl
            });
            if(!WAPLUS_REMOVE_FLAG){
                //todo
                const waplusUrl = 'https://wawebsender.com/sender-user-guide?utm_source=sender_install'
                chrome.tabs.create({
                    url: waplusUrl
                });
            }
        } else if (details.reason === 'update'){
            console.log("更新打印")
            updateSetInstallTime(installTime)
            if(!WAPLUS_REMOVE_FLAG) {
                loadZbaseConfigToStorage(extension_id, zbaseConfigUrl)
            }
        }
    });

    //redirect-link
    if (details.reason === 'install') {
        chrome.storage.sync.set({initialDialog: { show:true, opened:false }})
        chrome.storage.sync.set({reviewInvitationDialog:{opened:false,installTime:new Date().getTime()/1000}})
    }
    chrome.storage.sync.set({navRedirectButton: {show: true}})
});


